import React from "react";
import { NavLink } from "react-router-dom";

const QueryForm = () => {
  return (
    <>
      <div>
        {/* Contact Form */}
        <div className="outer-box" id="contact-form">
          <div className="inner-box" style={{ transform: "translateY(4px)" }}>
            <NavLink to="/contact">
              <i className="fa-regular fa-circle-xmark form-icon close" />
            </NavLink>
            <header className="form-header">
              <h1>Contact Us</h1>
              <p>"Empowering Your Shopping Experience!"</p>
            </header>
            <main className="form-body">
              <form action="#">
                <p className="input-box">
                  <i className="fa-solid fa-user form-icon input-field-icon" />
                  <input
                    type="text"
                    id="name"
                    placeholder="Enter Name"
                    required
                  />
                </p>
                <p className="input-box">
                  <i className="fa-solid fa-envelope form-icon input-field-icon" />
                  <input
                    type="email"
                    id="contact-email"
                    placeholder="Enter Email"
                    required
                  />
                </p>
                <p className="input-box">
                  <i className="fa-solid fa-phone form-icon input-field-icon" />
                  <input
                    type="number"
                    id="contact-number"
                    placeholder="Enter Phone Number"
                    required
                  />
                </p>
                <p className="input-box">
                  <i className="fa-solid fa-message form-icon input-field-icon" />
                  <textarea
                    id="message"
                    name="message"
                    rows={4}
                    placeholder="Drop your queries here."
                    required
                    defaultValue={""}
                  />
                </p>
                <p id="form-button">
                  <button value="Submit">Submit</button>
                </p>
              </form>
            </main>
            <footer className="form-footer">
              <p>
                Feel free to browse the website :{" "}
                <NavLink to="/">Bazaar Buddy</NavLink>
              </p>
            </footer>
          </div>
        </div>
      </div>
    </>
  );
};

export default QueryForm;
