import React, { useState, useEffect } from "react";
import Layout from "../components/Layout/Layout";
import TopBanner from "../components/TopBanner";
import Categories from "../components/Categories";
import axios from "axios";
import toast from "react-hot-toast";
import { Link } from "react-router-dom";
import AdminMenu from "../components/Layout/AdminMenu";
import { useAuth } from "../context/auth";
import { useCart } from "../context/cart";

const HomePage = () => {
  const [auth] = useAuth();

  const [products, setProducts] = useState([]);
  const [cart, setCart] = useCart();

  //   Get all products
  const getAllProducts = async () => {
    try {
      const { data } = await axios.get("/api/v1/product/all-products");
      setProducts(data.products);
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong");
    }
  };
  useEffect(() => {
    getAllProducts();
  }, []);

  return (
    <>
      <TopBanner />
      <Layout>
        <Categories />
        <div>
          {/* TagLine */}
          <section className="tagline-section">
            <div className="small-bold-text tagline-header">
              "Trusted by Top Companies and Esteemed Sellers: Your Premier
              Destination to Buy as well as Sell"
            </div>
          </section>
          {/* Hero Section */}
          <section className="home-hero-section">
            <h2 className="home-hero-header">
              "Step into a realm of limitless choices and exceptional quality."
            </h2>
          </section>

          <div className="home-products-container">
            <div className="col3 all-products">
              <h2>All Products</h2>
              <div className="product-card-container">
                {products?.map((p) => (
                  <div className="product-card">
                    <div className="product-head flex">
                      <img
                        src={`/api/v1/product/product-photo/${p._id}`}
                        alt={p.name}
                        className="product-head-image"
                      />
                      <div className="action-container flex">
                        <div className="add-to-cart action">
                          <i
                            class="fa-solid fa-cart-plus"
                            onClick={() => {
                              setCart([...cart, p]);
                              localStorage.setItem(
                                "cart",
                                JSON.stringify([...cart], p)
                              );
                              toast.success("Item added to Cart");
                            }}
                          ></i>
                        </div>
                        <div className="add-to-wishlist action">
                          <i class="fa-solid fa-heart"></i>
                        </div>
                      </div>
                    </div>
                    <div className="product-card-body">
                      <h2 className="product-card-title">{p.name}</h2>
                      <div className="flex product-pq">
                        <div className="product-price">
                          <div>
                            <b>Price</b>
                          </div>
                          Rs.{p.price}
                        </div>
                        <div className="product-quantity">
                          <div>
                            <b>Stock</b>
                          </div>{" "}
                          {p.quantity} units
                        </div>
                      </div>
                      <p className="product-card-description">
                        {p.description}
                      </p>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </Layout>
    </>
  );
};

export default HomePage;
