import React from "react";
import { NavLink } from "react-router-dom";
import Layout from "../components/Layout/Layout";

const PageNotFound = () => {
  return (
    <>
      <Layout>
        <div className="pnf-outer-container">
          <div className="pnf-inner-container flex">
            <img
              src="/images/HeroBackgrounds/Error404.png"
              alt="Error 404"
              id="pnf-image"
            />
            <div className="pnf-text">
              Oops! It seems you've reached a dead end in our virtual space. The
              page you were searching for has vanished into thin air. We
              apologize for the inconvenience. Please try the following:
              <ol start={1}>
                <li>Double-check the URL for any typos.</li>
                <li>Navigate back to our homepage and explore from there.</li>
                <li>
                  Contact our support team if you need immediate assistance.
                </li>
              </ol>
              We're working diligently to resolve this issue. Thank you for your
              understanding.
              <div className="pnf-links flex">
                <NavLink
                  to="/"
                  className="pnf-button hover-link"
                  id="return-home"
                >
                  Return Home
                </NavLink>
                <NavLink
                  to="/contact"
                  className="pnf-button hover-link"
                  id="contact-support"
                >
                  Need Help?
                </NavLink>
              </div>
            </div>
          </div>
        </div>
      </Layout>
    </>
  );
};

export default PageNotFound;
