import React, { useState, useEffect } from "react";
import Layout from "./../../components/Layout/Layout";
import AdminMenu from "../../components/Layout/AdminMenu";
import { useAuth } from "../../context/auth";
import toast from "react-hot-toast";
import axios from "axios";
import { Select } from "antd";
import TextArea from "antd/es/input/TextArea";
import { useNavigate, useParams } from "react-router-dom";
const { Option } = Select;

const UpdateProduct = () => {
  const navigate = useNavigate();
  const params = useParams();
  const [auth] = useAuth();
  const [categories, setCategories] = useState([]);
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [price, setPrice] = useState("");
  const [category, setCategory] = useState("");
  const [quantity, setQuantity] = useState("");
  const [shipping, setShipping] = useState("");
  const [photo, setPhoto] = useState("");
  const [id, setId] = useState("");

  //   Get single Product
  const getSingleProduct = async () => {
    try {
      const { data } = await axios.get(
        `/api/v1/product/single-product/${params.slug}`
      );
      setName(data.product.name);
      setId(data.product.id);
      setDescription(data.product.description);
      setPrice(data.product.price);
      setQuantity(data.product.quantity);
      setCategory(data.product.category._id);
      setShipping(data.product.shipping);
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    getSingleProduct();
  }, []);

  // Get all categories
  const getAllCategories = async () => {
    try {
      const { data } = await axios.get("/api/v1/category/all-categories");
      if (data?.success) {
        setCategories(data?.category);
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong in fetching categories");
    }
  };

  useEffect(() => {
    getAllCategories();
  }, []);

  // Update Product
  const handleUpdateProduct = async (event) => {
    event.preventDefault();
    try {
      const productData = new FormData();
      productData.append("name", name);
      productData.append("description", description);
      productData.append("price", price);
      productData.append("quantity", quantity);
      photo && productData.append("photo", photo);
      productData.append("category", category);
      productData.append("shipping", shipping);
      const { data } = axios.put(
        `/api/v1/product/update-product/${id}`,
        productData
      );
      if (data?.success) {
        toast.error(data?.message);
      } else {
        toast.success("Product updated!");
        navigate("/dashboard/admin/products");
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong in updating product");
    }
  };

  //   Delete Product
  const handleDeleteProduct = async () => {
    try {
      let answer = window.prompt("Do you really want to delete this product?");
      if (!answer) return;
      const { data } = await axios.delete(
        `/api/v1/product/delete-product/${id}`
      );
      toast.success("Product Removed!");
      navigate("/dashboard/admin/products");
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong in deleting product");
    }
  };

  return (
    <Layout>
      <div className="dashboard-container">
        <div className="col1 flex">
          <AdminMenu />
          <div className="dashboard-container">
            <div className="dashboard-container">
              <div className="user-card">
                <div className="user-card-header">
                  <h1>User Details</h1>
                </div>
                <div className="user-card-content">
                  <div className="user-card-details">
                    <div>
                      <p>
                        <strong>Name:</strong> {auth?.user?.name}
                      </p>
                      <p>
                        <strong>Email:</strong> {auth?.user?.email}
                      </p>
                      <p>
                        <strong>Phone:</strong> {auth?.user?.phone}
                      </p>
                      <p>
                        <strong>Address:</strong> {auth?.user?.address}
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="col2">
          <h2 className="flex">Manage Products</h2>
          <div className="flex product-manage-menu">
            <Select
              className="product-dropdown"
              placeholder="Select a Category"
              size="large"
              showSearch
              onChange={(value) => {
                setCategory(value);
              }}
              value={category}
            >
              {categories.map((c) => (
                <Option key={c._id} value={c._id} className="product-list">
                  {c.name}
                </Option>
              ))}
            </Select>
            <div className="upload-image flex">
              <label className="upload-button">
                {photo ? photo.name : "Upload Product Photo"}
                <input
                  type="file"
                  name="photo"
                  accept="image/*"
                  onChange={(event) => setPhoto(event.target.files[0])}
                  hidden
                />
              </label>
            </div>
            <div>
              {photo ? (
                <div>
                  <img
                    src={URL.createObjectURL(photo)}
                    alt="Product-photo-new"
                    height={"200px"}
                    className="image-up"
                  />
                </div>
              ) : (
                <div>
                  <img
                    src={`/api/v1/product/product-photo/${id}`}
                    alt="Product-photo-old"
                    height={"200px"}
                    className="image-up"
                  />
                </div>
              )}
            </div>
            <div className="form-control flex">
              <input
                type="text"
                value={name}
                placeholder="Product Name"
                className="form-control-field"
                onChange={(event) => setName(event.target.value)}
              />
            </div>
            <div className="form-control flex">
              <TextArea
                value={description}
                placeholder="Product Description"
                className="form-control-field"
                onChange={(event) => setDescription(event.target.value)}
              />
            </div>
            <div className="form-control flex">
              <input
                type="number"
                value={price}
                placeholder="Product Price"
                className="form-control-field"
                onChange={(event) => setPrice(event.target.value)}
              />
            </div>
            <div className="form-control flex">
              <input
                type="number"
                value={quantity}
                placeholder="Product Quantity"
                className="form-control-field"
                onChange={(event) => setQuantity(event.target.value)}
              />
            </div>
            <div className="form-control flex">
              <Select
                className="shipment-dropdown"
                placeholder="Product Shipment"
                size="large"
                showSearch
                onChange={(value) => setShipping(value)}
                value={shipping ? "Available" : "Not Available"}
              >
                <Option value="0">Not Available</Option>
                <Option value="1">Available</Option>
              </Select>
            </div>
            <div>
              <button
                type="submit"
                className="add-product-btn"
                onClick={handleUpdateProduct}
              >
                Update Product
              </button>
              <button
                type="submit"
                className="add-product-btn"
                onClick={handleDeleteProduct}
              >
                Delete Product
              </button>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default UpdateProduct;
