import React, { useState, useEffect } from "react";
import Layout from "./../../components/Layout/Layout";
import AdminMenu from "../../components/Layout/AdminMenu";
import { useAuth } from "../../context/auth";
import axios from "axios";
import toast from "react-hot-toast";
import { Link } from "react-router-dom";

const Products = () => {
  const [auth] = useAuth();
  const [products, setProducts] = useState([]);

  //   Get all products
  const getAllProducts = async () => {
    try {
      const { data } = await axios.get("/api/v1/product/all-products");
      setProducts(data.products);
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong");
    }
  };
  useEffect(() => {
    getAllProducts();
  }, []);

  return (
    <Layout>
      <div className="dashboard-container">
        <div className="col1 flex">
          <AdminMenu />
          <div className="dashboard-container">
            <div className="dashboard-container">
              <div className="user-card">
                <div className="user-card-header">
                  <h1>Admin Details</h1>
                </div>
                <div className="user-card-content">
                  <div className="user-card-details">
                    <div>
                      <p>
                        <strong>Name:</strong> {auth?.user?.name}
                      </p>
                      <p>
                        <strong>Email:</strong> {auth?.user?.email}
                      </p>
                      <p>
                        <strong>Phone:</strong> {auth?.user?.phone}
                      </p>
                      <p>
                        <strong>Address:</strong> {auth?.user?.address}
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="col2 all-products">
          <h2>All Products</h2>
          <div className="product-card-container">
            {products?.map((p) => (
              <Link key={p._id} to={`/dashboard/admin/product/${p.slug}`}>
                <div className="product-card">
                  <img
                    src={`/api/v1/product/product-photo/${p._id}`}
                    alt={p.name}
                    className="product-card-image"
                  />
                  <div className="product-card-body">
                    <h2 className="product-card-title">{p.name}</h2>
                    <div className="flex product-pq">
                      <div className="product-price">
                        <div><b>Price</b></div>Rs.{p.price}
                      </div>
                      <div className="product-quantity">
                        <div><b>Stock</b></div> {p.quantity} units
                      </div>
                    </div>
                    <p className="product-card-description">{p.description}</p>
                  </div>
                </div>
              </Link>
            ))}
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Products;
