import React from "react";
import Layout from "../components/Layout/Layout";
import { NavLink } from "react-router-dom";

const Contact = () => {
  return (
    <Layout>
      <div>
        {/* Hero Section */}
        <section className="contactus-hero-section" style={{ marginTop: 56 }}>
          <h2 className="contactus-hero-header">
            "Your Satisfaction, Our Priority : Unleashing Exceptional Customer
            Care!"
          </h2>
          {/* Contact Ways */}
          <div className="contact-container flex">
            <div className="contact-way flex way1">
              <a href="#" className="flex">
                <img
                  src="/images/Icons/ContactUs/Call.png"
                  alt
                  className="contact-way-icon"
                />
              </a>
              <div>
                <h3>Call</h3>
                <h5>78886 19369</h5>
              </div>
              <a href="#" className="flex">
                <img
                  src="/images/Icons/ContactUs/CustomerCare.png"
                  alt
                  className="contact-way-icon"
                />
              </a>
              <div>
                <h3>SMS</h3>
                <h5>78886 19369</h5>
              </div>
              <a href="#" className="flex">
                <img
                  src="/images/Icons/ContactUs/SMS.png"
                  alt
                  className="contact-way-icon"
                />
              </a>
            </div>
            <div className="contact-form-button flex">
              <NavLink to="/query" className="flex contact-button">
                <img
                  src="/images/Icons/ContactUs/Form.png"
                  alt="Contact Form"
                  className="contact-way-icon contact-form-icon"
                />
                <h3>Query</h3>
              </NavLink>
            </div>
            <div className="contact-way flex way2">
              <a href="#" className="flex">
                <img
                  src="/images/Icons/ContactUs/Email.png"
                  alt
                  className="contact-way-icon"
                />
              </a>
              <div>
                <h3>Email</h3>
                <h5>shivagoel2001 @gmail.com</h5>
              </div>
              <a href="#" className="flex">
                <img
                  src="/images/Icons/ContactUs/Queries.png"
                  alt
                  className="contact-way-icon"
                />
              </a>
              <div>
                <h3>Post</h3>
                <h5>#350, Phase4, Mohali</h5>
              </div>
              <a href="#" className="flex">
                <img
                  src="/images/Icons/ContactUs/PostBox.png"
                  alt
                  className="contact-way-icon"
                />
              </a>
            </div>
          </div>
        </section>
      </div>
    </Layout>
  );
};

export default Contact;
