import React, { useState } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import axios from "axios";
import toast from "react-hot-toast";

const RegisterationForm = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [cpassword, setCPassword] = useState("");
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");
  const navigate = useNavigate();

  // Form Submission Function
  const handleSubmit = async (event) => {
    event.preventDefault();
    try {
      const res = await axios.post("/api/v1/auth/register", {
        name,
        email,
        password,
        cpassword,
        phone,
        address,
      });
      if (res.data && res.data.success) {
        navigate("/login");
        toast.success(res.data && res.data.message);
      } else {
        toast.error(res.data.message);
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong!");
    }
  };

  return (
    <>
      <div>
        {/* Registration Form */}
        <div className="outer-box" id="registeration-form">
          <div className="inner-box" style={{ transform: "translateY(1.5%)" }}>
            <NavLink to="/">
              <i className="fa-regular fa-circle-xmark form-icon close" />
            </NavLink>
            <header className="form-header">
              <h1>Register</h1>
              <p>It takes less than a minute!</p>
            </header>
            <main className="form-body">
              <form onSubmit={handleSubmit}>
                <p className="input-box">
                  <i className="fa-solid fa-user form-icon input-field-icon" />
                  <input
                    type="text"
                    value={name}
                    onChange={(event) => setName(event.target.value)}
                    id="new-username"
                    placeholder="Enter your name"
                    required
                  />
                </p>
                <p className="input-box">
                  <i className="fa-solid fa-envelope form-icon input-field-icon" />
                  <input
                    type="email"
                    value={email}
                    onChange={(event) => setEmail(event.target.value)}
                    id="email-id"
                    placeholder="Enter your email"
                    required
                  />
                </p>
                <p className="input-box">
                  <i className="fa-solid fa-lock form-icon input-field-icon" />
                  <input
                    type="password"
                    value={password}
                    onChange={(event) => setPassword(event.target.value)}
                    id="new-password"
                    placeholder="Enter new password"
                    required
                  />
                  <i
                    className="fa-solid fa-eye form-icon hide-pass"
                    id="show-new-pass"
                  />
                  {/* <i className="fa-solid fa-eye-slash form-icon" id="hide-pass" /> */}
                </p>
                <p className="input-box">
                  <i className="fa-solid fa-lock form-icon input-field-icon" />
                  <input
                    type="password"
                    value={cpassword}
                    onChange={(event) => setCPassword(event.target.value)}
                    id="confirm-password"
                    placeholder="Confirm password"
                    required
                  />
                  <i
                    className="fa-solid fa-eye form-icon hide-pass"
                    id="show-confirm-pass"
                  />
                  {/* <i class="fa-solid fa-eye-slash form-icon" id="hide-pass"></i> */}
                </p>
                <p className="input-box">
                  <i className="fa-solid fa-address-book form-icon input-field-icon" />
                  <input
                    type="number"
                    value={phone}
                    onChange={(event) => setPhone(event.target.value)}
                    id="contact-number"
                    placeholder="Enter your contact"
                    required
                  />
                </p>
                <p className="input-box">
                  <i className="fa-solid fa-house-chimney form-icon input-field-icon" />
                  <input
                    type="text"
                    value={address}
                    onChange={(event) => setAddress(event.target.value)}
                    id="user-address"
                    placeholder="Enter your address"
                    required
                  />
                </p>
                <p id="form-button">
                  <button value="Register">Sign Up</button>
                </p>
              </form>
            </main>
            <footer className="form-footer">
              <p>
                Already have an account?{" "}
                <NavLink to="/login" className="login-button">
                  Login
                </NavLink>
              </p>
            </footer>
          </div>
        </div>
      </div>
    </>
  );
};

export default RegisterationForm;
