import React, { useEffect, useState } from "react";
import Layout from "./../../components/Layout/Layout";
import AdminMenu from "../../components/Layout/AdminMenu";
import { useAuth } from "../../context/auth";
import toast from "react-hot-toast";
import axios from "axios";
import CategoryForm from "../../components/CategoryForm";
import { Modal } from "antd";

const CreateCategory = () => {
  const [auth] = useAuth();
  const [categories, setCategories] = useState([]);
  const [name, setName] = useState("");
  const [visible, setVisible] = useState(false);
  const [selected, setSelected] = useState(null);
  const [updatedName, setUpdatedName] = useState("");

  // Handle Form
  const handleSubmit = async (event) => {
    event.preventDefault();
    try {
      const { data } = await axios.post("/api/v1/category/create-category", {
        name,
      });
      if (data?.success) {
        toast.success(`${name} created!`);
        getAllCategories();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong while adding category");
    }
  };

  // Get all categories
  const getAllCategories = async () => {
    try {
      const { data } = await axios.get("/api/v1/category/all-categories");
      if (data?.success) {
        setCategories(data?.category);
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong in fetching categories");
    }
  };

  useEffect(() => {
    getAllCategories();
  }, []);

  // Handle Updates
  const handleUpdate = async (event) => {
    event.preventDefault();
    try {
      const { data } = await axios.put(
        `/api/v1/category/update-category/${selected._id}`,
        { name: updatedName }
      );
      if (data.success) {
        toast.success(`${updatedName} updated!`);
        setSelected(null);
        setUpdatedName("");
        setVisible(false);
        getAllCategories();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong while updating category!");
    }
  };

  // Remove Categories
  const handleDelete = async (id) => {
    try {
      const { data } = await axios.delete(
        `/api/v1/category/delete-category/${id}`
      );
      if (data.success) {
        toast.success("Category Deleted!");
        getAllCategories();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong while updating category!");
    }
  };

  return (
    <Layout>
      <div className="dashboard-container">
        <div className="col1 flex">
          <AdminMenu />
          <div className="dashboard-container">
            <div className="dashboard-container">
              <div className="user-card">
                <div className="user-card-header">
                  <h1>User Details</h1>
                </div>
                <div className="user-card-content">
                  <div className="user-card-details">
                    <div>
                      <p>
                        <strong>Name:</strong> {auth?.user?.name}
                      </p>
                      <p>
                        <strong>Email:</strong> {auth?.user?.email}
                      </p>
                      <p>
                        <strong>Phone:</strong> {auth?.user?.phone}
                      </p>
                      <p>
                        <strong>Address:</strong> {auth?.user?.address}
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="col2">
          <h2 className="flex">Manage Categories</h2>
          <div>
            <CategoryForm
              handleSubmit={handleSubmit}
              value={name}
              setValue={setName}
            />
          </div>
          <div className="flex">
            <table className="category-table">
              <thead className="category-table-head">
                <tr className="category-table-headings">
                  <th scope="col">Category Name</th>
                  <th scope="col">Actions</th>
                </tr>
              </thead>
              <tbody className="category-table-body">
                {categories?.map((c) => (
                  <>
                    <tr className="category-table-rows">
                      <td key={c._id}>{c.name}</td>
                      <td>
                        <button
                          className="category-action-btn"
                          id="category-update-btn"
                          onClick={() => {
                            setVisible(true);
                            setUpdatedName(c.name);
                            setSelected(c);
                          }}
                        >
                          <i class="fa-solid fa-pen"></i>
                        </button>
                        <button
                          className="category-action-btn"
                          id="category-delete-btn"
                          onClick={() => {
                            handleDelete(c._id);
                          }}
                        >
                          <i class="fa-solid fa-trash"></i>
                        </button>
                      </td>
                    </tr>
                  </>
                ))}
              </tbody>
            </table>
          </div>
          <Modal
            onCancel={() => setVisible(false)}
            footer={null}
            open={visible}
          >
            <CategoryForm
              value={updatedName}
              setValue={setUpdatedName}
              handleSubmit={handleUpdate}
            />
          </Modal>
        </div>
      </div>
    </Layout>
  );
};

export default CreateCategory;
