import React from "react";
import { Link } from "react-router-dom";

const Footer = () => {
  return (
    <footer className="page-footer">
      <h3>All rights reserved &copy; Shiva Goel</h3>
      <h4>2023</h4>
      <p>
        <Link to="https://github.com/invincible777/" className="footer-link">
          Github
        </Link>
        |<Link to="/about">About Us</Link>|<Link to="/policy">Policy</Link>
      </p>
    </footer>
  );
};

export default Footer;
