import React from "react";
import { Link, NavLink } from "react-router-dom";
import { useAuth } from "../../context/auth";
import { toast } from "react-hot-toast";

const Header = () => {
  const [auth, setAuth] = useAuth();

  const handleLogout = () => {
    setAuth({
      ...auth,
      user: null,
      token: "",
    });
    localStorage.removeItem("auth");
    toast.success("Logged out successfully!");
  };

  return (
    <>
      {/*  Navbar */}
      <nav className="main-nav flex" id="navbar">
        <div className="nav-toggle">
          <i class="fa-solid fa-bars"></i>
        </div>
        <div className="search-section flex">
          <Link to="/" className="company-logo">
            <img src="/images/Logos/MainLogo.png" alt="Company-Logo" />
          </Link>
          <p id="brandname">Bazaar &nbsp; Buddy</p>
          <span>
            <input
              type="text"
              placeholder="Search here"
              className="search-box"
            />
            <input type="button" defaultValue="Search" className="search-btn" />
          </span>
        </div>
        <div className="nav-links">
          <ul className="flex">
            <li>
              <NavLink to="/" className="hover-link nav-item">
                Home
              </NavLink>
            </li>
            <div className="dropdown">
              {!auth.user ? (
                <>
                  <li>
                    <NavLink
                      to={`/dashboard/${
                        auth?.user?.role === 1 ? "admin" : "user"
                      }`}
                      className="hover-link nav-item"
                    >
                      User
                    </NavLink>
                  </li>
                  <div className="dropdown-content">
                    <NavLink to="/register" className="register-button">
                      Register
                    </NavLink>
                    <NavLink to="/login" className="login-button">
                      Login
                    </NavLink>
                  </div>
                </>
              ) : (
                <>
                  <li>
                    <NavLink
                      to={`/dashboard/${
                        auth?.user?.role === 1 ? "admin" : "user"
                      }`}
                      className="hover-link nav-item"
                    >
                      {auth.user.name}
                    </NavLink>
                  </li>
                  <div className="dropdown-content">
                    <NavLink to="/orders">Orders</NavLink>
                    <NavLink onClick={handleLogout} to="/login">
                      Logout
                    </NavLink>
                  </div>
                </>
              )}
            </div>
            <div className="dropdown">
              <li>
                <NavLink to="/basket" className="hover-link nav-item">
                  Basket
                </NavLink>
              </li>
              <div className="dropdown-content">
                <NavLink to="/cart">Cart</NavLink>
                <NavLink to="/wishlist">WishList</NavLink>
              </div>
            </div>
            <li>
              <NavLink
                to="/contact"
                href="contactus.html"
                className="hover-link nav-item"
              >
                Contact Us
              </NavLink>
            </li>
          </ul>
        </div>
      </nav>
    </>
  );
};

export default Header;
