import React from "react";
import { NavLink } from "react-router-dom";

const UserMenu = () => {
  return (
    <>
      <div className="sidebar flex">
        <div className="admin-menu flex">
          <h3>User Panel</h3>
          <NavLink to="/dashboard/user/orders" className="admin-menu-item">
            Orders
          </NavLink>
          <NavLink to="/dashboard/user/wishlist" className="admin-menu-item">
            Wishlist
          </NavLink>
        </div>
      </div>
    </>
  );
};

export default UserMenu;
