import React, { useState } from "react";
import { NavLink, useNavigate, useLocation } from "react-router-dom";
import axios from "axios";
import toast from "react-hot-toast";
import { useAuth } from "../context/auth";

const LoginForm = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [auth, setAuth] = useAuth();

  const navigate = useNavigate();
  const location = useLocation();

  // Form Submission Function
  const handleSubmit = async (event) => {
    event.preventDefault();
    try {
      const res = await axios.post("/api/v1/auth/login", {
        email,
        password,
      });
      if (res.data.success) {
        setAuth({
          ...auth,
          user: res.data.user,
          token: res.data.token,
        });
        localStorage.setItem("auth", JSON.stringify(res.data));
        navigate(location.state || "/");
        toast.success(res.data.message);
      } else {
        toast.error(res.data.message);
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong!");
    }
  };

  return (
    <>
      <div className="outer-box" id="login-form">
        <div className="inner-box" style={{ transform: "translateY(10%)" }}>
          <NavLink to="/">
            <i className="fa-regular fa-circle-xmark form-icon close" />
          </NavLink>
          <header className="form-header">
            <h1>Login</h1>
            <p>Quicker than you think!</p>
          </header>
          <main className="form-body">
            <form onSubmit={handleSubmit}>
              <p className="input-box">
                <i className="fa-solid fa-envelope form-icon input-field-icon" />
                <input
                  type="email"
                  value={email}
                  onChange={(event) => setEmail(event.target.value)}
                  id="username"
                  placeholder="Enter your email"
                  required
                />
              </p>
              <p className="input-box">
                <i className="fa-solid fa-lock form-icon input-field-icon" />
                <input
                  type="password"
                  value={password}
                  onChange={(event) => setPassword(event.target.value)}
                  id="password"
                  placeholder="Enter your password"
                  required
                />
                <i
                  className="fa-solid fa-eye form-icon hide-pass"
                  id="show-pass"
                />
                {/*<i className="fa-solid fa-eye-slash form-icon" id="hide-pass" />*/}
              </p>
              <div className="option-field">
                <span className="checkbox">
                  <input type="checkbox" id="check" />
                  <label htmlFor="check">Remember me</label>
                </span>
                <a>
                  <NavLink to="/forgot-pass" className="forgot-pass">
                    Forgot Password?
                  </NavLink>
                </a>
              </div>
              <p id="form-button">
                <button value="Login">Login Now</button>
              </p>
            </form>
          </main>
          <footer className="form-footer">
            <p>
              First time here? &nbsp;
              <NavLink to="/register" className="register-button">
                Register
              </NavLink>
            </p>
          </footer>
        </div>
      </div>
    </>
  );
};

export default LoginForm;
