import React from "react";
import Header from "./Header";
import Footer from "./Footer";
import QuickLinks from "../QuickLinks";
import { Toaster } from "react-hot-toast";

const Layout = (props) => {
  return (
    <div>
      <Header />
      <main style={{ minHeight: "77.8vh" }}>
        <Toaster
          toastOptions={{
            // Define default options
            className: "",
            duration: 5000,
            style: {
              background: "#363636",
              color: "#fff",
            },

            // Default options for specific types
            success: {
              duration: 4000,
              theme: {
                primary: "green",
                secondary: "black",
              },
            },
          }}
        />
        {props.children}
      </main>
      <QuickLinks />
      <Footer />
    </div>
  );
};

export default Layout;
