import React from "react";
import { NavLink } from "react-router-dom";

const Categories = () => {
  return (
    <>
      {/* Sub Navbar */}
      <header>
        <nav className="sub-nav flex" id="subnavbar">
          <ul className="flex">
            <li>
              <NavLink to="/fruits" className="hover-link flex subnav-item">
                <img
                  src="/images/Grocery/fruits.png"
                  className="subnav-item-img"
                  alt=""
                />
                Fruits
              </NavLink>
            </li>
            <li>
              <NavLink to="/vegetables" className="hover-link flex subnav-item">
                <img
                  src="/images/Grocery/vegetables.png"
                  className="subnav-item-img"
                  alt=""
                />
                Vegetables
              </NavLink>
            </li>
            <li>
              <NavLink
                to="/all-in-one-pcs"
                className="hover-link flex subnav-item"
              >
                <img
                  src="/images/Electronics/all-in-one-pcs.png"
                  className="subnav-item-img"
                  alt=""
                />
                All-in-one PCs
              </NavLink>
            </li>
            <li>
              <NavLink to="/desktops" className="hover-link flex subnav-item">
                <img
                  src="/images/Electronics/desktops.png"
                  className="subnav-item-img"
                  alt=""
                />
                Desktops
              </NavLink>
            </li>
            <li>
              <NavLink to="/laptops" className="hover-link flex subnav-item">
                <img
                  src="/images/Electronics/laptops.png"
                  className="subnav-item-img"
                  alt=""
                />
                Laptops
              </NavLink>
            </li>
            <li>
              <NavLink
                to="/smart-phones"
                className="hover-link flex subnav-item"
              >
                <img
                  src="/images/Electronics/smartphones.png"
                  className="subnav-item-img"
                  alt=""
                />
                Smart Phones
              </NavLink>
            </li>
            <li>
              <NavLink
                to="/smart-watches"
                className="hover-link flex subnav-item"
              >
                <img
                  src="/images/Electronics/smartwatches.png"
                  className="subnav-item-img"
                  alt=""
                />
                Smart Watches
              </NavLink>
            </li>
          </ul>
        </nav>
      </header>
    </>
  );
};

export default Categories;
