import React from "react";
import Layout from "../components/Layout/Layout";
import { useCart } from "../context/cart";
import { useAuth } from "../context/auth";
import { useNavigate } from "react-router-dom";

const CartPage = () => {
  const [cart, setCart] = useCart();
  const [auth, setAuth] = useAuth();
  const navigate = useNavigate();

  const totalPrice = () => {
    try {
      let total = 0;
      cart?.map((item) => {
        total = total + item.price;
      });
      return total;
    } catch (error) {
      console.log(error);
    }
  };

  const removeCartItem = (pid) => {
    try {
      let myCart = [...cart];
      let index = myCart.findIndex((item) => item._id === pid);
      myCart.splice(index, 1);
      setCart(myCart);
      localStorage.setItem("cart", JSON.stringify(myCart));
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <Layout>
      <div className="">
        <div className="cart-main flex">
          <div className="cart-head flex">
            <h2>{`Hello ${auth?.token && auth?.user?.name}`}</h2>
            <h4>
              {cart?.length
                ? `You have ${cart.length} items in your cart. ${
                    auth?.token ? "" : "Please login to checkout!"
                  }`
                : "Your cart is empty!"}
            </h4>
          </div>
          <h3>Your total amount is : Rs {totalPrice()}</h3>
          <div className="home-products-container">
            <div className="col3 all-products">
              <h2>Cart Items</h2>
              <div className="product-card-container">
                {cart?.map((p) => (
                  <div className="product-card">
                    <div className="product-head flex">
                      <img
                        src={`/api/v1/product/product-photo/${p._id}`}
                        alt={p.name}
                        className="product-head-image"
                      />
                    </div>
                    <div className="product-card-body">
                      <h2 className="product-card-title">{p.name}</h2>
                      <div className="flex">
                        <div className="product-price">
                          <div>
                            <b>Price</b>
                          </div>
                          Rs.{p.price}
                        </div>
                      </div>
                      <p className="product-card-description">
                        {p.description.substring(0, 30)}
                      </p>
                      <button
                        className="category-action-btn"
                        id="category-delete-btn"
                        onClick={() => {
                          removeCartItem(p._id);
                        }}
                      >
                        <i class="fa-solid fa-trash"></i>
                      </button>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default CartPage;
